@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
@import url(https://fonts.googleapis.com/css?family=Anton:400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* h1 {
    font-family: 'Open', sans-serif;
    font-size: 2.5rem;
    font-weight: 400;
    margin-top: 0;
    letter-spacing:2px;
}  */

.sub-title {
    font-family: 'Open', sans-serif;
    font-size: 1.4rem;
}

.container {
    max-width: 1024px;
    margin: 0 auto;
}

h1 {
    font-family: 'Open', sans-serif;
    font-weight: 700;
    font-size: 2em;
    font-weight: 400;
    margin-top: 0;
    letter-spacing: 2px;
}

. {
    height: 10em;
}

@media (min-width: 768px) {
    
    h1 {
        text-align: center;
        font-size: 4rem;
    }

    .sub-title {
        font-family: 'Open', sans-serif;
        font-size: 1.8rem;
    }
  }
.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 8rem;
}

.dv {
    width: 45rem;
    height: 400px;
    background-color: #fff;
}

.dv h3 {
    font-family: 'Nunito', sans-serif;
    padding: .5rem;
    text-align: center;
}

.dv p {
    padding: 2rem;
    text-align: left;
}

.media {
    margin: 0.5rem;
    /* padding: 1em; */
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.media i {
    font-size: 3rem;
    color: #2494c1;
    margin: 10px;
}

.fab {
    font-family: "Font Awesome 5 Brands";
}


@media (min-width: 768px) {

    
    .media {
        justify-content: left;
    }

    .dv {
        margin: 3rem;
    }

    .david h1 {
        padding: .5rem;
    }

    .dv h3 {
       text-align: left;
    }

    .dv p {
        padding: .5rem;
    }
    
}
.card {
    width: 22rem;
    overflow: hidden;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 10px 20px #eee, 0 6px 6px #d5d5d5;
    margin-top: 3rem;
}

.card h3 {
    font-family: nunito,sans-serif;
    text-align: center;
    padding: 1rem;
    
}

.card a {
    text-decoration: none;
    color: #000;
}

.card p {
    font-family: open sans,sans-serif;
    font-size: .9rem;
    font-weight: 300;
    letter-spacing: 1px;
    padding: 15px;
}



.image-bg {
    width: 22rem;
    height: 10rem;
    background-position: top;
}

.sv {
    background-image: url(/portfolio/static/media/sergiovacca_ss.c7a4c961.jpg);
}

.fl {
    background-image: url(/portfolio/static/media/fl_ss_r.ceb418c3.jpg);
}

.ba {
    background-image: url(/portfolio/static/media/ba_ss_r.7d6db2e5.jpg);
}

.bjc {
    background-image: url(/portfolio/static/media/bjc_ss.f491911e.jpg);
}

@media (min-width: 768px) {
    .card {
        margin: 3rem;
    }
}
footer {
    background: #F0BB49;
    background-image: url(/portfolio/static/media/sfondo-header.8cf58cc3.jpg);
    width: 100%;
    height: 70px;
    background-position: center;
    background-size: cover;
    margin-top: 150px;
    padding: 25px;
}

.footer-text {
    text-align: center;
    font-family: open sans,sans-serif;
    font-size: 1.1rem;
    color: #fff;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}

.App h1, p {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-image: url(/portfolio/static/media/sfondo-header.8cf58cc3.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  text-shadow: 0 0 10px rgba(0,0,0,.2);
  padding: 80px 0;
  height: 10em;
}

.App-link {
  color: #61dafb;
}


  .david h1 {
    font-family: 'Anton', sans-serif;
    font-size: 3rem;
    padding: 0.5rem;
}



@media (min-width: 768px) {
    
  .App h1, p {
    text-align: left;
  }

  .App-header {
    height: auto;
  }

  .sub-title {
    font-family: open,sans-serif;
    font-size: 1.8rem;
  }
}

